import React, { PureComponent, Suspense } from "react";
import { componentWillAppendToBody } from "react-append-to-body";
import { connect } from 'react-redux';
import { isMobile } from 'react-device-detect';
import { ASTLoadData, SMVThemes, TASTLoadData, TSMVThemes, TagShopLoadData, TagShopThemes, SOCWALThemes } from '../../constants'
import { THEME_WIDTH_ADJUSTMENT_MODERN, URLDATA, findFromArray, reelPlayTimeUpdate, IS_THEME } from '../../utils'
import { showPopUP, getDataNextSteps, themePostTracking, gtmAnalyticsAction, closePopUP, managePostHeight } from '../../actions/themeActions'
import { suiteTrack, isTagEmbed, isTagShop, isTagShopEditor, isSocialWall, isEmbed, isSocialEmbed, isTagbox } from "../../actions/api";

const SeeMore = React.lazy(() => import("./Elements/SeeMore/SeeMore"));
const ThemeBranding = React.lazy(() => import("./Elements/Branding/ThemeBranding"));
const TikTok_Img_update = React.lazy(() => import('../Components/customFunction/tikTok_Img_update'));
const NewsCard = React.lazy(() => import("./NewsCard"));
const ClassicPhoto = React.lazy(() => import("./ClassicPhoto"));
const ClassicCard = React.lazy(() => import("./ClassicCard"));
const ModernCard = React.lazy(() => import("./ModernCard"));
const SquarePhoto = React.lazy(() => import("./SquarePhoto"));
const WidgetTheme = React.lazy(() => import("./WidgetTheme"));
const GalleryPhoto = React.lazy(() => import("./GalleryPhoto"));
const GalleryTheme = React.lazy(() => import("./GalleryTheme"));
const CollageTheme = React.lazy(() => import("./CollageTheme"));


const ReelTheme = React.lazy(() => import('./ReelTheme'));
const ReelShop = React.lazy(() => import('./ReelShop'));
const HighlightTheme = React.lazy(() => import('./HighlightTheme'));
const SinglePost = React.lazy(() => import('./SinglePost'));
const StoryTheme = React.lazy(() => import('./StoryTheme'));
const HorizontalSlider = React.lazy(() => import('./HorizontalSlider'));
const HorizontalColumns = React.lazy(() => import('./HorizontalColumns'));
const Badge = React.lazy(() => import("./Badge"));
const RatingBadge = React.lazy(() => import("./RatingBadge"));
const ReviewCarousel = React.lazy(() => import("./ReviewCarousel"));

const Cascade = React.lazy(() => import("./Waterfall/Cascade"));
const Exhibit = React.lazy(() => import("./Waterfall/Exhibit"));
const Focus = React.lazy(() => import("./Waterfall/Focus"));
const Pixel = React.lazy(() => import("./Waterfall/Pixel"));
const RoundEdge = React.lazy(() => import("./Waterfall/RoundEdge"));

const GFocus = React.lazy(() => import("./Grid/Focus"));
const GPixel = React.lazy(() => import("./Grid/Pixel"));
const GReels = React.lazy(() => import("./Grid/Reels"));
const GSquare = React.lazy(() => import("./Grid/Square"));

const SlideShow = React.lazy(() => import("./SlideShow"));


const CHorizontal = React.lazy(() => import("./Carousel/Horizontal"));
const CMotion = React.lazy(() => import("./Carousel/Motion"));
const CReels = React.lazy(() => import("./Carousel/Reels"));
const ReactRipple = React.lazy(() => import("./Elements/Reactions/ReactRipple"));

const MyComponent = ({ children }) => {
  return children;
}
const AppendedView = componentWillAppendToBody(MyComponent);


const AutoScrollThemeEnableLoadData = isTagEmbed ? TASTLoadData : isTagShop ? TagShopLoadData : isSocialWall ? SOCWALThemes : ASTLoadData
const ShowMoreVisibleThemes = isTagEmbed ? TSMVThemes : isTagShop ? TagShopThemes : isSocialEmbed ? SOCWALThemes : SMVThemes

const ThemeRender = React.memo(({ renderId, wall, reviewFilters, webFilters, postData, adjustWidth, languageSetting, clickToShowPopUp, cardNumber, onClickToCTA, postSizeInPx, onClickPopUpSlider, parentID, keyRender, containerSize, heightUpdate }) => {

  const hasMoreData = postData.hasMoreData[0] ? postData.hasMoreData[0].hasMoreData == false ? false : postData.hasMoreData[postData.appendData.networkID] ? postData.hasMoreData[postData.appendData.networkID].hasMoreData : true : true
  const themeProps = { ...postData, reviewFilters, webFilters: webFilters, hasMoreData: hasMoreData, wall: wall, adjustWidth: adjustWidth, languageSetting: languageSetting, clickToShowPopUp: clickToShowPopUp, onClickToCTA: onClickToCTA, onClickPopUpSlider, keyRender, containerSize, heightUpdate }

  switch (wall.Personalization.widgetTheme) {
    case 20:
      return adjustWidth ? <Suspense fallback={<></>}><ModernCard {...themeProps} /></Suspense> : null
    case 5:
      return adjustWidth ? <Suspense fallback={<></>}><ClassicCard {...themeProps} /></Suspense> : null
    case 3:
      return adjustWidth ? <Suspense fallback={<></>}><ClassicPhoto {...themeProps} /></Suspense> : null
    case 4:
      return adjustWidth ? <Suspense fallback={<></>}><SquarePhoto {...themeProps} cardNumber={cardNumber} postSizeInPx={postSizeInPx} /> </Suspense> : null
    case 19:
    case 59:
    case 83:
      return adjustWidth ? <Suspense fallback={<></>}><NewsCard {...themeProps} /> </Suspense> : null
    case 50:
      return isTagShop ? <Suspense fallback={<></>}><GalleryTheme {...themeProps} cardNumber={cardNumber} renderId={renderId} postSizeInPx={postSizeInPx} /></Suspense> : <Suspense fallback={<></>}><GalleryPhoto {...themeProps} cardNumber={cardNumber} renderId={renderId} postSizeInPx={postSizeInPx} /></Suspense>
    case 52:
    case 79:
      return isTagEmbed || isTagbox && IS_THEME(79) ? adjustWidth ? <Suspense fallback={<></>}><ModernCard {...themeProps} /></Suspense> : null : <Suspense fallback={<></>}><SinglePost {...themeProps} /></Suspense>
    case 16:
      return <Suspense fallback={<></>}><HorizontalSlider {...themeProps} postSizeInPx={postSizeInPx} onClickPopUpSlider={onClickPopUpSlider} parentID={parentID} /></Suspense>
    case 55:
    case 81:
      return <Suspense fallback={<></>}><HighlightTheme {...themeProps} cardNumber={parseInt(cardNumber)} onClickPopUpSlider={onClickPopUpSlider} parentID={parentID} /></Suspense>
    case 49:
    case 53:
    case 85:
      return isTagShop ? <Suspense fallback={<></>}><CollageTheme {...themeProps} cardNumber={cardNumber} renderId={renderId} postSizeInPx={postSizeInPx} /></Suspense> : <Suspense fallback={<></>}><WidgetTheme {...themeProps} /></Suspense>
    case 47:
      return <Suspense fallback={<></>}><HorizontalColumns {...themeProps} cardNumber={cardNumber} onClickPopUpSlider={onClickPopUpSlider} renderId={renderId} parentID={parentID} /></Suspense>
    case 54:
    case 80:
      return <Suspense fallback={<></>}><ReviewCarousel {...themeProps} onClickPopUpSlider={onClickPopUpSlider} renderId={renderId} parentID={parentID} /></Suspense>
    case 6:
    case 61:
      return <Suspense fallback={<></>}><ReelTheme {...themeProps} onClickPopUpSlider={onClickPopUpSlider} renderId={renderId} parentID={parentID} /></Suspense>
    case 62:
      return isTagShop ? <Suspense fallback={<></>}><ReelShop {...themeProps} cardNumber={cardNumber} onClickPopUpSlider={onClickPopUpSlider} renderId={renderId} parentID={parentID} /></Suspense> : isSocialWall ? <Suspense fallback={<></>}>
        <Cascade {...themeProps} onClickPopUpSlider={onClickPopUpSlider} parentID={parentID} />
        {/* <ClassicCard {...themeProps} onClickPopUpSlider={onClickPopUpSlider} parentID={parentID} /> */}
      </Suspense> : ''
    case 57:
    case 84:
      return <Suspense fallback={<></>}><Badge {...themeProps} /></Suspense>
    case 58:
    case 82:
      return <Suspense fallback={<></>}><RatingBadge {...themeProps} /></Suspense>
    case 60:
      return <Suspense fallback={<></>}><StoryTheme {...themeProps} onClickPopUpSlider={onClickPopUpSlider} parentID={parentID} /></Suspense>
    case 56:
      return <Suspense fallback={<></>}><StoryTheme {...themeProps} onClickPopUpSlider={onClickPopUpSlider} parentID={parentID} /></Suspense>
    case 63:
      return adjustWidth ? <Suspense fallback={<></>}>
        <Focus {...themeProps} onClickPopUpSlider={onClickPopUpSlider} parentID={parentID} />
      </Suspense> : null
    case 64:
      return <Suspense fallback={<></>}><Exhibit cardNumber={cardNumber} {...themeProps} onClickPopUpSlider={onClickPopUpSlider} parentID={parentID} /></Suspense>
    case 65:
      return <Suspense fallback={<></>}>
        <Pixel {...themeProps} onClickPopUpSlider={onClickPopUpSlider} parentID={parentID} cardNumber={cardNumber} postSizeInPx={postSizeInPx} />
      </Suspense>
    case 78:
      return <Suspense fallback={<></>}>
        <RoundEdge {...themeProps} onClickPopUpSlider={onClickPopUpSlider} parentID={parentID} />
      </Suspense>
    case 66:
      return <Suspense fallback={<></>}>
        <GSquare {...themeProps} onClickPopUpSlider={onClickPopUpSlider} parentID={parentID} />
      </Suspense>
    case 67:
      return <Suspense fallback={<></>}>
        <GPixel {...themeProps} onClickPopUpSlider={onClickPopUpSlider} parentID={parentID} cardNumber={cardNumber} postSizeInPx={postSizeInPx} />
      </Suspense>
    case 68:
      return <Suspense fallback={<></>}>
        <GFocus {...themeProps} onClickPopUpSlider={onClickPopUpSlider} parentID={parentID} cardNumber={cardNumber} renderId={renderId} postSizeInPx={postSizeInPx} />
      </Suspense>
    case 69:
      return <Suspense fallback={<></>}><GReels {...themeProps} onClickPopUpSlider={onClickPopUpSlider} parentID={parentID} /></Suspense>
    case 74:
    case 76:
    case 77:
      return <Suspense fallback={<></>}><SlideShow {...themeProps} onClickPopUpSlider={onClickPopUpSlider} parentID={parentID} /></Suspense>
    case 70:
      return <Suspense fallback={<></>}>
        <CHorizontal {...themeProps} onClickPopUpSlider={onClickPopUpSlider} parentID={parentID} renderId={renderId} />
      </Suspense>
    case 71:
      return <Suspense fallback={<></>}>
        {isSocialEmbed ? <HorizontalSlider {...themeProps} postSizeInPx={postSizeInPx} onClickPopUpSlider={onClickPopUpSlider} parentID={parentID} /> : <CMotion {...themeProps} onClickPopUpSlider={onClickPopUpSlider} postSizeInPx={postSizeInPx} parentID={parentID} />}
      </Suspense>
    case 73:
      return <Suspense fallback={<></>}><CReels {...themeProps} onClickPopUpSlider={onClickPopUpSlider} parentID={parentID} /></Suspense>
    default:
      return <></>
  }
})
class ThemeRoute extends PureComponent {
  state = {
    windowWidth: window.innerWidth,
    windowHeight: window.innerHeight,
    adjustWidth: 0,
    cardNumber: 0,
    postSizeInPx: 0,
    isLoaded: false, currentIndex: -1,
    height: 0
  }
  componentWillMount() {
    const { renderId } = this.props.renderId
    this.setState({
      windowWidth: renderId && document.getElementById(renderId) ? document.getElementById(renderId).clientWidth : window.innerWidth
    })
  }
  componentDidMount() {
    const { renderId } = this.props.renderId
    this.onResponsiveTheme();
    if (isSocialWall && !isEmbed && !window.location.href.includes("onsite-upload")) {
      const { wall: { ThemeRule: { timeForPopup }, Personalization } } = this.props;
      const timer = timeForPopup === 0 ? 5000 : timeForPopup * 1000;
      if (Personalization.postFeatured == 1) setTimeout(() => this.loadPopUpWithTimer(), timer)
    }
    window.addEventListener && window.addEventListener("resize", () => {
      this.setState({ windowWidth: renderId && document.getElementById(renderId) ? document.getElementById(renderId)?.clientWidth : window.innerWidth, windowHeight: window.innerHeight }, () => this.onResponsiveTheme())
    });
    if (window.editor) {
      let wallEditor = document.getElementsByClassName("wall-editor");
      wallEditor.length > 0 && wallEditor[0].addEventListener("scroll", this.autoLoadEditorScrollData);
    }
    else {
      window.addEventListener && window.addEventListener("scroll", this.autoLoadScrollData);
    }
    // setTimeout(() => {
    //   this.setState({
    //     isLoaded: true
    //   })
    // }, 1000);
    if (isTagShop && !isTagShopEditor && isSocialWall) reelPlayTimeUpdate()

  }
  heightUpdate = () => {
    if (!isSocialEmbed) {
      const getHeight = (selector) => (document.querySelector(selector) || {}).clientHeight || 0;
      const themeBanner = getHeight(".tb_wall_header__");
      const editorHeader = window.editor ? getHeight("#kt_header") : 0;
      const ticker = getHeight(".ti_container");
      this.setState({ height: themeBanner + editorHeader + ticker })
    }
  };
  componentWillReceiveProps(newProps) {
    if (newProps != this.props && isTagShopEditor) this.onResponsiveTheme();
  }
  autoLoadEditorScrollData = event => {
    const { wall, postData, loaderData } = this.props;
    const postCount = wall.ThemeRule.numberOfPosts;
    let wallEditor = document.getElementsByClassName("wall-editor");
    if (wall.Personalization.autoScrollStatus == 1 && AutoScrollThemeEnableLoadData.includes(wall.Personalization.widgetTheme) && wallEditor.length > 0) {
      let fixedHeightScrollBar = wallEditor[0];
      let documentHeight = document.body.scrollHeight;
      if ((documentHeight + fixedHeightScrollBar.scrollTop) >= (fixedHeightScrollBar.scrollHeight - 10)) {
        if (!(loaderData && loaderData.isShowMoreLoading)) {
          const timeStamp = Math.floor(Date.now() / 1000);
          this.props.getDataNextSteps(wall.Wall.id, timeStamp, postCount, postData.appendData.networkID, postData.appendData.after, postData.appendData.heightEvent);
        }
      }
    }
  }
  autoLoadScrollData = event => {
    const { wall, postData, loaderData } = this.props;
    const postCount = wall.ThemeRule.numberOfPosts;
    const body = document.body;
    const html = document.documentElement;
    if (wall.Personalization.autoScrollStatus === 1 && AutoScrollThemeEnableLoadData.includes(wall.Personalization.widgetTheme)) {
      const height = Math.max(body.scrollHeight, body.offsetHeight, html.clientHeight, html.scrollHeight, html.offsetHeight);
      if ((window.innerHeight + window.pageYOffset) >= (parseInt(height) - parseInt(50))) {
        if (!(loaderData && loaderData.isShowMoreLoading)) {
          const timeStamp = Math.floor(Date.now() / 1000);
          this.props.getDataNextSteps(wall.Wall.id, timeStamp, postCount, postData.appendData.networkID, postData.appendData.after, postData.appendData.heightEvent);
        }
      }
    }
  }
  onResponsiveTheme = (openAside) => {
    const { wall } = this.props;
    let { windowWidth } = this.state;
    windowWidth = openAside === 2 ? windowWidth + 165 : windowWidth
    let { adjustWidth, cardNumber } = THEME_WIDTH_ADJUSTMENT_MODERN(windowWidth, wall);
    let postWidth = 100 / windowWidth * adjustWidth;
    adjustWidth = postWidth.toFixed(2);
    this.setState({ adjustWidth, cardNumber, postSizeInPx: THEME_WIDTH_ADJUSTMENT_MODERN(windowWidth, wall) }, () => this.heightUpdate())
  }
  onClickToShowPopup = (currentIndex, item) => event => {
    event.stopPropagation();
    this.onLoadPopUp(currentIndex, item)
  }
  onClickPopUpSlider = (currentIndex, item) => this.onLoadPopUp(currentIndex, item)
  onLoadPopUp = (currentIndex, item) => {
    //event.stopPropagation();
    const { wall, languageSetting, postData, } = this.props;
    const isUserType = wall?.User?.userDatabaseType == "tagembed"
    if (!isUserType && !isTagShop && ((isMobile && !wall.Personalization.mobilePopup) || ((isUserType && wall.Personalization.mobilePopup) && item.link !== ""))) {
      if (item.link == 'undefined') return false
      window.open(item.link, '_blank')
    }
    else {
      let updatePostData = postData.postData.map(pItem => postData.completeDataObject[pItem])
      let filteredPostData = updatePostData.filter((filterData) => !String(filterData.id).includes("free_add_"))
      let nIndex = findFromArray(filteredPostData, item.id, currentIndex)
      if (wall.Personalization.postFeatured === 2) {
        if (item.link == 'undefined' || item.link == '') return false
        window.open(item.link, '_blank')
      }
      else if (wall.Personalization.postFeatured === 3) window.open(wall.Personalization.customUrl, '_blank');
      else {
        if (wall.Personalization.postFeatured === 1) this.props.showPopUP({ type: 'post', card: item, idArray: filteredPostData, index: nIndex, viewOnText: languageSetting.viewOnText, shareText: languageSetting.shareText, personalization: wall.Personalization, imgData: URLDATA, ThemeRule: wall.ThemeRule, wall: wall })
      }
    }
    const requestData = { feed: item.feedId, post: item.referenceId ? item.referenceId : item.id }
    const tagembedPayload = { wallId: wall.Wall.id, userId: wall.Wall.owner, feedId: item.feedId, postId: item.id }
    const ugcPayload = { action: 2, wall: wall.Wall.id, feed: item.feedId, post: item.referenceId ? item.referenceId : item.id, type: 3, owner: wall.Wall.owner }

    themePostTracking(isTagEmbed ? tagembedPayload : suiteTrack ? ugcPayload : { action: 2, wall: wall.Wall.id, ...requestData, owner: wall.Wall.owner })
    gtmAnalyticsAction("Tagbox", "popupclick_", "popup", item.link)
    // }
  }

  loadPopUpWithTimer = () => {
    const { currentIndex } = this.state;
    const { postData: { postData, completeDataObject }, wall: { ThemeRule: { timeForPopup } } } = this.props;
    const timer = timeForPopup === 0 ? 5000 : timeForPopup * 1000;
    let nextIndex = currentIndex + 1;
    if (nextIndex >= postData.length) nextIndex = 0;
    if (!isNaN(nextIndex) && postData.length > 0) {
      const updatePostData = postData.map((pItem) => completeDataObject[pItem]);
      this.setState({ currentIndex: nextIndex });
      this.togglePopUpVisibility(nextIndex, updatePostData[nextIndex], timer);
    }
  };

  togglePopUpVisibility = (index, data, timer) => {
    this.onLoadPopUp(index, data);
    setTimeout(() => {
      this.props.closePopUP();
      setTimeout(() => {
        this.loadPopUpWithTimer();
      }, timer);
    }, timer);
  };


  onClickToCTA = (item) => {
    const { wall } = this.props;
    if (!isTagEmbed) themePostTracking({ action: 2, wall: wall.Wall.id, type: 1, feed: item.feedId, post: item.referenceId ? item.referenceId : item.id, owner: wall.Wall.owner })
    gtmAnalyticsAction("Tagbox", "ctaClick", "cta", item.cta && item.cta.url ? item.cta.url : "")
  }

  onLoadeMore = event => {
    const { wall, appendData, postHeight } = this.props;
    const postCountTagshop = wall.ThemeRule.numberOfCoumn < 5 ? 5 : wall.ThemeRule.numberOfCoumn

    const postCount = isTagShop && wall.Personalization.widgetTheme == 53 ? postCountTagshop : wall.ThemeRule.numberOfPosts;
    const timeStamp = Math.floor(Date.now() / 1000);
    this.props.managePostHeight(postHeight);
    this.props.getDataNextSteps(wall.Wall.id, timeStamp, postCount, appendData.networkID, appendData.after, appendData.heightEvent);

  }

  render() {
    const { wall, languageSetting, postData, containerSize, brandingNetworks, parentID, isBranding, loaderData } = this.props;
    window.onResponsiveTheme = this.onResponsiveTheme
    const { isLoaded, height } = this.state
    const hideBranding = isTagEmbed && (wall.Personalization.widgetTheme == 58 || wall.Personalization.widgetTheme == 57) ? false : true
    const hasMoreData = postData.hasMoreData && Object.keys(postData.hasMoreData).length > 0 ? postData.hasMoreData[0] && postData.hasMoreData[0].hasMoreData == false ? false : postData.hasMoreData[postData.appendData.networkID] ? postData.hasMoreData[postData.appendData.networkID].hasMoreData : true : true


    return <>
      <div className={`tb_theme_container ${isSocialWall && !isSocialEmbed ? 'tb_wall_container' : ''}`} style={{ height: isSocialWall && !isSocialEmbed ? `calc(100vh - ${height}px)` : null }}>
        {postData.isFreeAdsStatus && !isTagShop && hideBranding && !isSocialWall && postData?.completeData?.length > 0 ? <Suspense fallback={<></>}><ThemeBranding containerSize={containerSize} languageSetting={languageSetting} brandingNetworks={brandingNetworks} wallId={wall.Wall.id} themeID={wall.Personalization.widgetTheme} user={wall.User.userDatabaseType} /> </Suspense> : null}
        <ThemeRender {...this.props} {...this.state} clickToShowPopUp={this.onClickToShowPopup} onClickToCTA={this.onClickToCTA} onClickPopUpSlider={this.onClickPopUpSlider} parentID={parentID} onLoadPopUp={this.onLoadPopUp} 
        // heightUpdate={this.heightUpdate} key={postData?.postData?.length} 
        {...(isSocialWall && { 
          heightUpdate: this.heightUpdate, 
          key: postData?.postData?.length 
      })}
        />
        {postData.isFreeAdsStatus && isTagShop && hideBranding && postData?.completeData?.length > 0 ? <Suspense fallback={<></>}><ThemeBranding containerSize={containerSize} languageSetting={languageSetting} brandingNetworks={brandingNetworks} wallId={wall.Wall.id} themeID={wall.Personalization.widgetTheme} /> </Suspense> : null}

        { wall.Personalization.loadMoreStatus === 1 && ShowMoreVisibleThemes.includes(wall.Personalization.widgetTheme) ? <Suspense fallback={<></>}>
        <SeeMore languageSetting={languageSetting} wall={wall} appendData={postData.appendData} postData={postData} isBranding={postData.isFreeAdsStatus && isTagShop ? true : false} /></Suspense> : null}
        {/* {postData && postData.completeDataObject ? <Suspense fallback={<></>}><TikTok_Img_update completeDataObject={postData.completeDataObject} postData={postData.postData} /></Suspense> : ''} */}

        {(isSocialWall && wall?.Personalization?.interactive && !isEmbed) ? <AppendedView><Suspense fallback={<></>}><ReactRipple /></Suspense></AppendedView> : null}



      </div>
    </>
  }
}


const mapStateToProps = state => {
  return {
    loaderData: state.loaderData,
    renderId: state.renderId,
    postHeight: state.postHeight.data
  }
}
const mapDispatchToProps = dispatch => {
  return {
    showPopUP: (data) => dispatch(showPopUP(data)),
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow)),
    closePopUP: () => dispatch(closePopUP()),
    managePostHeight: (data) => dispatch(managePostHeight(data)),
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow))
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(ThemeRoute);